@import url(https://fonts.googleapis.com/css2?family=Georama&family=Gowun+Dodum&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{height:100vh;display:flex;background-color:#fff;align-items:center;justify-content:center;margin:auto;flex-direction:column}.quote{background-color:#008CBA;height:350px;width:450px;max-width:100%;display:flex;justify-content:center;align-items:center;flex-direction:column;border-radius:10px;padding:50px}.quote #text{font-size:1.5em;font-family:'Georama', sans-serif}.quote #author{font-size:1.1em;font-family:'Georama', sans-serif}.btn{border:2px solid #008CBA;padding:10px 15px;color:black;font-size:20px;background-color:white;border-radius:5px;cursor:pointer;outline:none;transition:background 1s, color 1s;margin-top:10px;margin-bottom:10px}.btn:hover{background:#01b9f7;color:#fff;box-shadow:0 8px 16px 0 rgba(0,0,0,0.2),0 6px 20px 0 rgba(0,0,0,0.19)}a{border:2px solid #008CBA;padding:10px 15px;color:black;font-size:20px;background-color:white;border-radius:5px;cursor:pointer;outline:none;transition:background 1s, color 1s;margin-top:10px;margin-bottom:10px;text-decoration:none}a:hover{background:#01b9f7;color:#fff;box-shadow:0 8px 16px 0 rgba(0,0,0,0.2),0 6px 20px 0 rgba(0,0,0,0.19)}

